import React, { useEffect, useState } from 'react'
import Container from 'react-bootstrap/esm/Container';
import Row from 'react-bootstrap/esm/Row';
import Col from 'react-bootstrap/esm/Col';
import { Fade } from "react-awesome-reveal";

import './main.css'


function Main() {



  return (
    <Container>
       
        <Row>
          
       
            <Col id='main' md={12} lg={12}>
            <Fade duration={1000}>
                <div className='main-info visibleMain'>
                    <h1> " Przedszkole to drugi dom</h1>
                    <h2>w ktrym Wasze dzieci <br></br> 
                      spdzaj duo czasu, ucz si i rozwijaj, <br></br>
                      a nauczyciele peni rol rodzicw... "
                    </h2>
                </div>
              </Fade>
              
                
            </Col>
           
        </Row>
<Fade  delay={500}>
        <Row className='main-info-container visible-main-info-container'>
          
          <Col > 
            <h2>Opaty za korzystanie z przedszkola</h2>
            <p>Od stycznia 2023 roku zmianie ulegaj opaty za korzystanie z przedszkola:</p>
            <p>Dzienna stawka ywieniowa wynosi:</p>
            <ul>
              <li>3 posiki (niadanie, obiad, podwieczorek) - 100% 10,00 z</li>
              <li>2 posiki (niadanie, obiad) - 75 % 7,50 z</li>
              <li>2 posiki (obiad, podwieczorek) - 85 % 8,50 z</li>
              <li> 1 posiek (obiad) - 60 % 6,00 z</li>
            </ul>
            <p>Miesiczna opata za wiadczenia wynosi: </p>
            <p> 1,30 z za kad dodatkow godzin,</p> 
            <p> 0,65 z dla rodzin objtych programem Krakowska Karta Rodzinna 3 + lub Krakw dla Rodziny N </p>
            <p> ponad 5 bezpatnych godzin , miedzy 8:00-13:00</p>
            
         </Col>
         
         
          <Col className='main-info2'> <h2>Wane informacje</h2>
          <div>
              <h3>Grypa w przedszkolu</h3>
              <p>Szanowni Rodzice zachcamy do zapoznania sie z Oglnopolskim Program Zwalczania Grypy. Broszura do pobrania w formacie pdf pdf A tutaj link do strony programu.</p>
          </div>
          </Col>
        </Row>
        </Fade>
        
        
    </Container>
  )
}

export default Main