import React, { useState } from 'react'
import Container from 'react-bootstrap/esm/Container'
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Fade } from 'react-awesome-reveal';


function AboutUs() {  

  return (
    <Container id='about'>
      <Fade delay={500}>
       <Row>
        
        
      <Col>
          <div className='about-info visible' id='about-info'>
            <h3>O  Nas</h3>
            <p>Przedszkole usytuowane jest w budynku pitrowym, wolnostojcym. Dysponuje kilkoma jasnymi obszernymi salami zaj, azienkami i du szatni. Na naszym terenie znajduje si pikny, zmodernizowany ogrd bogaty w ziele. Dodatkowo posiadamy bogat baz dydaktyczn oraz sprzt sportowy.Naszym celem jest wspomaganie i ukierunkowanie rozwoju dziecka zgodnie z jego wrodzonym potencjaem w relacjach ze rodowiskiem spoeczno-kulturowym i przyrodniczym.</p>

            <p>Gwarantujemy:</p>
              <ul>
                <li>odpowiednio dobrany zesp kompetentnych nauczycieli o wysokich kwalifikacjach i dowiadczeniu pedagogicznym</li>
                <li>gotowo penej wsppracy z rodzicami</li>
                <li>opracowany i znany rodzicom plan rozwoju przedszkola</li>
                <li>prowadzenie dziaalnoci w oparciu o "Podstaw Programow" obudowan nowatorskimi programami edukacyjnymi oraz autorskimi nauczycieli </li>

              </ul>
      </div> 
      </Col>

      <Col>

        <div className='about-info2 visible' id='about-info2'>
           <h3>Jeli wybierajc przedszkole dla swojego dziecka pragniecie aby:</h3>
            <ul>
                  <li>zapewnio wysoki poziom wychowania i ksztacenia</li>
                  <li>zachcao do dziaania i osignicia sukcesw</li>
                  <li>zapewniao bezpieczestwo       </li>
                  <li>   uatwiao wiadomo prozdrowotn    </li>
                  <li>     rozwijao wraliwo estetyczn  </li>
                  <li>       pogbiao zdolno twrczego mylenia</li>
                  <li>       rozbudzao potrzeb wspdziaania ze rodowiskiem spoeczno-kulturowym i przyrodniczym</li>
                  <li>       uwraliwio moralnie</li>
                  <li>       dyo do harmonizowania wiedzy o zdrowiu i jego profilaktyce</li>
                  <li>       rozwijao umiejtnoci i nawyki prozdrowotne</li>
                  <li>       uwiadamiao, e czowiek jest czci przyrody, podlega wszystkim jej prawom i jest od niej uzaleniony</li>
                  <li>       ksztatowao wasne "Ja" w harmonii ze rodowiskiem</li>
                  <li>       czyo aktywno ruchow z poznawaniem rodowiska przyrodniczego</li>
        
            </ul>
        </div>

      
       
      </Col>

       
       </Row>
       </Fade>
      </Container>
  )
}

export default AboutUs